footer {
  background-image: url("../img/footer-bg.jpg");
  background-size: cover;
  padding-top: 36px;
  padding-bottom: 30px;
  .footer-content {
    margin: 0 auto;
    text-align: center;
    color: #FFFFFF;
    h3 {
      font-family: RobotoCondensedBI, sans-serif;
      font-size: 27px;
      margin-bottom: 22px;
    }
    a {
      text-decoration: none;
      color: #FFFFFF;
      font-size: 25px;
      font-family: RobotoCondensedI, sans-serif;
      &:after {
        content: "|";
        margin: 0 5px;
      }
      &:last-child:after {
        content: "";
      }

    }
    p {
      margin-top: 30px;
      font-family: RobotoCondensedI, sans-serif;
      font-size: 18px;
    }
  }
}
